:root {
    --nav-bar-height: 10%;
}

body {
  background:-webkit-linear-gradient(bottom,rgba(56,165,247, 0.2),rgba(56,165,247,0.5)),url(/static/media/Ionia_Placidium_Navori.0fb876af.png);
  background:linear-gradient(0deg,rgba(56,165,247, 0.2),rgba(56,165,247,0.5)),url(/static/media/Ionia_Placidium_Navori.0fb876af.png);
  background-size: 100% 100%;
  background-repeat: repeat;
  background-attachment: fixed;
}

.landing-nav {
    display: flex;
    width: 100%;
    position: fixed;
    height: 10%;
    height: var(--nav-bar-height);
    align-items: top;
    background: #7386D5;
    color: #fff;
}

.landing-nav > a {
    padding-top: calc(10% / 2.2);
    padding-top: calc(var(--nav-bar-height) / 2.2);
    text-align: center;
    flex: 1 1;
    color: #fff;
    font-weight: 900;
    font-size: 1.2em;
    text-decoration: none;
    font-family: 'Bebas Nueue', 'Arial Narrow', Arial, sans-serif;
}

.landing-nav > a:hover {
    text-decoration: underline;
}

.landing-main {
    color: #fff;
    padding-top: 10%;
    padding-top: var(--nav-bar-height);
}

@supports (zoom:2) {
    input[type=checkbox] {
        zoom: 5;
    }    
}

.long-description-image {
  min-width: 150px;
  min-height: 150px;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url(/static/media/logo.ee7cd8ed.svg);  
}

.landing-logo {
  background-image: url(/static/media/logo.ee7cd8ed.svg);
  background-repeat: no-repeat;
  background-position: center center;
  display: inline-block;
  flex: 1 1;
}

.akali-picture {
    min-width: 150px;
    min-height: 150px;
    background-repeat: no-repeat;
    background-position: center center;
    background-image: url(/static/media/akali.13ecc94b.png);  
}

.toggle-checkbox {
    text-align: center;
    line-height: 200px;
    height: 200px;
}

.inner-checkbox {
    padding: 30px;
    display: inline-block;
    vertical-align: middle;
}

.landing-nav-end {
    text-align: center;
}


.landing-title-container {
    margin-top: 150px;
}

.landing-title, .landing-subtitle, .landing-plus {
    text-align: center;
    
}
.landing-title {
    font-size: 5em;
    font-family: 'Bebas Nueue', 'Arial Narrow', Arial, sans-serif;

}

.landing-subtitle {
    font-size : 1.5em;
}

.landing-plus {
    font-size: 5em;
    font-family: 'Bebas Nueue', 'Arial Narrow', Arial, sans-serif;
    margin: 0;
    position: relative;
    top: -20px;
}

.search-container {
    display: flex;
    justify-content: center;
    /* align-items: center; */
}

.search-names {
    width: 240px;
    display: flex;
    flex-flow:column;
    margin-left: 20px;
}

.search-name-field {
    display: block;
    padding: 0.7em 0.25em;
    border: none;
    border-radius: 8px;
    font-size: 1.2em;
    text-align: center;
